.navbar {
    background: rgba(0, 0, 0, 0.9);
    font-size: 1.8rem;
    border-bottom: solid 1px rgb(0, 81, 255);
}

.navbar-ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: right;
    overflow: hidden;
}

li {
    display: inline-block;
}

li a {
    color: rgb(210, 210, 210);
    display: inline-block;
    padding: 1.8rem 2rem 1.6rem 2rem;
    text-decoration: none;
}

button {
    background: transparent;
    border: solid 3px rgb(0, 81, 255);
    border-radius: 15px;
    color: rgb(210, 210, 210);
    font-size: 1.5rem;
    font-weight: 600;
    margin: 0 2rem 0 1rem;
    padding: 3px 20px 3px 20px;

    -webkit-transition: 0.3s;
    transition: 0.3s;
}

button:hover {
    background: rgb(0, 81, 255);
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.home {
    float: left;
}

.header {
    background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.6)),
        url(/Photo-site/static/media/nature.8bffe872.jpg);
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.6)),
        url(/Photo-site/static/media/nature.8bffe872.jpg);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    position: relative;
    width: 100%;
}

a {
    text-decoration: none;
}

.content-box {
    color: rgb(182, 182, 182);
    height: 20rem;
    font-family: Courgette;
    margin: auto;
    max-width: 80rem;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

.header-h1 {
    font-size: 3.8rem;
    font-style: italic;
}

.header-h2 {
    margin-top: 1.8rem;
    text-align: center;
}

.header-button {
    background: rgb(0, 81, 255);
    border: none;
    border-radius: 10px;
    color: rgb(210, 210, 210);
    display: block;
    font-size: 2rem;
    font-weight: 700;
    margin: auto;
    margin-top: 5rem;
    padding: 17px 50px;

    -webkit-transition: 0.4s;
    transition: 0.4s;
}
.header-button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

@media only screen and (max-width: 1025px) {
    .content-box {
        text-align: center;
        width: 100%;
    }
}

@media only screen and (max-width: 415px) {
    .content-box {
        max-width: 100%;
        margin: 200px auto;
        padding: 0 20px 0 20px;
        text-align: center;
    }
}

@media only screen and (max-width: 365px) {
    .content-box {
        margin: 60px auto;
    }
}

.equipment-header {
    padding: 25px;
}

.equipment {
    padding-bottom: 25px;
}

.equipment-row {
    text-align: center;
    background: rgb(190, 190, 190);
    box-shadow: 2px 6px 20px 10px rgba(0, 0, 0, 0.2);
}

.equip-icon {
    margin: 10px;
    color: rgb(0, 81, 255);
}

.equip-content {
    font-family: Raleway;
    padding: 5px 0px 20px 0px;
}

.col-mid {
    border-left: solid 1px black;
    border-right: solid 1px black;
}

@media only screen and (max-width: 415px) {
    .equip-icon {
        margin-top: 20px;
    }

    .col-mid {
        border-left: none;
        border-right: none;
        border-top: solid 1px black;
        border-bottom: solid 1px black;
    }

    .equip-content {
        padding: 0px 5px 15px 15px;
    }
}

.about {
    background: rgb(190, 190, 190);
    border-top: 1px solid rgb(0, 81, 255);
    padding: 25px;
}

.title-header {
    padding-bottom: 25px;
}

.about-words {
    font-family: Raleway;
    margin: auto;
    max-width: 600px;
    text-align: center;
    padding-bottom: 25px;
}

.image-box {
    background-image: url(/Photo-site/static/media/profile.f9ec8297.jpeg);
    margin: auto;
    height: 200px;
    width: 200px;
    border-radius: 50%;
    margin-bottom: 25px;
}

.title-h1 {
    text-align: center;
    font-family: merriweather;
    font-style: italic;
}

.photo-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 5px;
    grid-auto-rows: 11px;
}

.photo-list img {
    border-radius: 10px;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    width: 250px;
}

.photo-list img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

@media only screen and (max-width: 1025px) {
    .photo-list {
        justify-items: center;
    }
}

.gallery {
    background: rgb(85, 85, 85);
    border-top: 1px solid rgb(0, 81, 255);
    padding: 25px;
}
.gallery-container {
    max-width: 1020px;
    margin: auto;
}

@media only screen and (max-width: 1025px) {
    .gallery-container {
        max-width: 100%;
    }
}

.footer {
    background: rgba(0, 0, 0, 0.9);
    border-top: 1px solid rgb(0, 81, 255);
    color: rgb(182, 182, 182);
    font-family: Raleway;
    padding: 25px;
    text-align: center;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    font-size: 62.5%;
}

body {
    background: rgb(230, 230, 230);
    font-size: 1.6rem;
}

button {
    cursor: pointer;
}

.row {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
}
.row::after {
    display: block;
    clear: both;
    content: "";
}

.col-1 {
    width: 8.33%;
}
.col-2 {
    width: 16.66%;
}
.col-3 {
    width: 25%;
}
.col-4 {
    width: 33.33%;
}
.col-5 {
    width: 41.66%;
}
.col-6 {
    width: 50%;
}
.col-7 {
    width: 58.33%;
}
.col-8 {
    width: 66.66%;
}
.col-9 {
    width: 75%;
}
.col-10 {
    width: 83.33%;
}
.col-11 {
    width: 91.66%;
}
.col-12 {
    width: 100%;
}

.container {
    max-width: 80rem;
    margin: 0 auto;
    padding: 0 1.6rem;
}

@media only screen and (max-width: 1025px) {
    .container {
        max-width: 800px;
    }
}

@media only screen and (max-width: 415px) {
    .container {
        max-width: 370px;
        margin: 0 auto;
    }

    [class*="col-"] {
        width: 100%;
    }
}

